import {
  AlphanumericFilterPopup,
  DateFilterDropdown,
  MultiFilterDropdown,
  NumericFilterPopup,
  RemoteMultiFilterDropdown,
} from './FilterDropdowns';

export const filterTypes: {
  [filterType in string]: {
    filter: (item: any, filterField: string, filterValues: any) => boolean;
    component: React.ElementType;
  };
} = {
  multiFilter: {
    filter: (item, filterField, filterValues: string[] | undefined) => {
      if (!filterValues || filterValues.length === 0) {
        return true;
      }
      let itemFieldValues;
      if (!Array.isArray(item[filterField])) {
        itemFieldValues = [item[filterField]];
      } else {
        itemFieldValues = item[filterField];
      }
      return !itemFieldValues.every(
        (itemValue) => !filterValues.includes(itemValue)
      );
    },
    component: MultiFilterDropdown,
  },
  remoteMultiFilter: {
    filter: (item, filterField, filterValues: string[] | undefined) => {
      if (!filterValues || filterValues.length === 0) {
        return true;
      }
      let itemFieldValues;
      if (!Array.isArray(item[filterField])) {
        itemFieldValues = [item[filterField]];
      } else {
        itemFieldValues = item[filterField];
      }
      return !itemFieldValues.every(
        (itemValue) => !filterValues.includes(itemValue)
      );
    },
    component: RemoteMultiFilterDropdown,
  },
  dateFilter: {
    filter: (
      item,
      filterField,
      filterValues: { startDate: string; endDate: string }
    ) => {
      if (!filterValues) {
        return true;
      }
      const { startDate, endDate } = filterValues;
      const date = new Date(item[filterField]).toISOString().slice(0, 10);
      if (startDate && date <= new Date(startDate).toISOString().slice(0, 10)) {
        return false;
      }
      if (endDate && date >= new Date(endDate).toISOString().slice(0, 10)) {
        return false;
      }
      return true;
    },
    component: DateFilterDropdown,
  },
  numericFilter: {
    filter: comparisonFilter(numericComparisons),
    component: NumericFilterPopup,
  },
  alphanumericFilter: {
    filter: comparisonFilter(alphanumericComparisons),
    component: AlphanumericFilterPopup,
  },
};
const singleComparators = {
  null: true,
  '!null': true,
  empty: true,
  '!empty': true,
};
function comparisonFilter(comparisons) {
  return (item, filterField, filterValues) => {
    if (!filterValues) return true;
    let isValid = true;
    let orHasValue = false;
    for (const opts of filterValues) {
      if (opts.combination === '||') {
        if (isValid && orHasValue) break;
        isValid = true;
        orHasValue = false;
      } else if (isValid === false) {
        continue;
      }
      if (!(singleComparators[opts.comparison] || opts.value)) {
        continue;
      }
      orHasValue = true;
      if (!comparisons(opts.comparison, item[filterField], opts.value)) {
        isValid = false;
      }
    }
    return orHasValue && isValid;
  };
}
function numericComparisons(comparison, a, b) {
  /* eslint-disable eqeqeq */
  switch (comparison) {
    case '=':
      return a == b;
    case '!=':
      return a != b;
    case '>=':
      return a >= b;
    case '>':
      return a > b;
    case '<=':
      return a <= b;
    case '<':
      return a < b;
    case 'null':
      return a == null;
    case '!null':
      return a != null;
    default:
      return true;
  }
}

function alphanumericComparisons(comparison, a, b) {
  const stringA = String(a).toUpperCase();
  const stringB = String(b).toUpperCase();
  switch (comparison) {
    case '=':
      return stringA === stringB;
    case '!=':
      return stringA !== stringB;
    case 'contains':
      return stringA.includes(stringB);
    case '!contains':
      return !stringA.includes(stringB);
    case 'starts':
      return stringA.startsWith(stringB);
    case 'ends':
      return stringA.endsWith(stringB);
    case 'null':
      return a == null;
    case '!null':
      return a != null;
    case 'empty':
      return stringA === '';
    case '!empty':
      return stringA !== '';
    default:
      return true;
  }
}
