import Axios, { AxiosRequestConfig, Canceler } from 'axios';
import { useEffect, useMemo, useReducer } from 'react';

// /**
//  * @callback processData
//  * @param data
//  * @return processed Data
//  */
// /**
//  * @callback onError
//  * @param error
//  */

// /**
//  * @property  processData - If present, will process the data before setting it to the state. Must return the processed data.
//  * @property  onError
//  * @property  withCredentials
//  * @property  deps - If present, the axios call will be re-performed/cleanedup whenever deps changes
//  * @property  runIf - If Present, the axios call will not be performed if this returns false.
//  * @property  cacheKey - If Present, will store the result from the last axios call to the same URL with the same cacheKey
//  */

interface HookConfig<T, Y> {
  /**If present, will process the data before setting it to the state. Must return the processed data.*/
  processData?: (data: T) => Y;
  onError?: (error: Error) => void;
  withCredentials?: boolean;
  /**If present, the axios call will be re-performed/cleanedup whenever deps changes*/
  deps?: ReadonlyArray<any>;
  /**If present, the axios call will only be performed if this returns true.*/
  runIf?: () => boolean;
  /**If present, will store the result from the last axios call to the same URL with the same cacheKey*/
  cacheKey?: string;
}

const cache: { [key: string]: any } = {};
const keyDelim = '\u200C';

export function useAxios<T, Y = T>(
  config: AxiosRequestConfig,
  hookConfig: HookConfig<T, Y> = {}
): AxiosState<Y> & { trigger: () => void } {
  const reducer = useMemo(() => axiosReducer<Y>(), []);
  const [state, dispatch] = useReducer(reducer, {
    loading: true,
    error: {},
  });
  const [forceTrigger, trigger] = useReducer((x) => x + 1, 0);
  const {
    processData,
    onError,
    withCredentials,
    deps = [],
    runIf,
    cacheKey,
  } = hookConfig;
  useEffect(() => {
    if (runIf && !runIf()) {
      dispatch({ type: 'noRun' });
      return;
    }
    const url = config.url;
    const key = `${url}${keyDelim}${cacheKey}${keyDelim}${deps.join(keyDelim)}`;
    if (cacheKey && cache[key]) {
      let data = cache[key];
      data = processData ? processData(data) : data;
      dispatch({ type: 'data', payload: data });
      return;
    }
    let cancelToken: Canceler | undefined;
    // setLoading(true);
    dispatch({ type: 'loading' });
    Axios.request({
      withCredentials,
      ...config,
      cancelToken: new Axios.CancelToken((c) => (cancelToken = c)),
    }).then(
      (res) => {
        if (cacheKey) {
          cache[key] = res.data;
        }
        const data = processData ? processData(res.data) : res.data;
        dispatch({ type: 'data', payload: data });
      },
      (err) => {
        if (Axios.isCancel(err)) return;
        dispatch({ type: 'error', payload: err });
        onError && onError(err);
      }
    );
    return cancelToken;
    // eslint-disable-next-line
  }, [...deps, forceTrigger]);
  return { ...state, trigger };
}

/**
 * @param url - Url to query
 * @param  params - Parameters
 * @param  hookConfig
 */
export function useAxiosGet<T, Y = T>(
  url: string,
  params?: AxiosRequestConfig['params'],
  hookConfig: HookConfig<T, Y> = {}
): AxiosState<Y> & { trigger: () => void } {
  const reducer = useMemo(() => axiosReducer<Y>(), []);
  const [state, dispatch] = useReducer(reducer, {
    loading: true,
    error: {},
  });
  const [forceTrigger, trigger] = useReducer((x) => x + 1, 0);
  const {
    processData,
    onError,
    withCredentials,
    deps = [],
    runIf,
    cacheKey,
  } = hookConfig;
  useEffect(() => {
    if (runIf && !runIf()) {
      dispatch({ type: 'noRun' });
      return;
    }
    const key = `${url}${keyDelim}${cacheKey}${keyDelim}${deps.join(keyDelim)}`;
    if (cacheKey && cache[key]) {
      let data = cache[key];
      data = processData ? processData(data) : data;
      dispatch({ type: 'data', payload: data });
      return;
    }
    let cancelToken: Canceler | undefined;
    dispatch({ type: 'loading' });
    Axios.get(url, {
      withCredentials,
      params,
      cancelToken: new Axios.CancelToken((c) => (cancelToken = c)),
    }).then(
      (res) => {
        if (cacheKey) {
          cache[key] = res.data;
        }
        const data = processData ? processData(res.data) : res.data;
        dispatch({ type: 'data', payload: data });
      },
      (err) => {
        if (Axios.isCancel(err)) return;
        dispatch({ type: 'error', payload: err });
        onError && onError(err);
      }
    );
    return cancelToken;
    // eslint-disable-next-line
  }, [...deps, forceTrigger]);
  return { ...state, trigger };
}

type AxiosState<T> = { error?: object; data?: T; loading: boolean };

type Actions<T> =
  | { type: 'loading' }
  | { type: 'error'; payload: object }
  | { type: 'data'; payload: T }
  | { type: 'noRun' };

const axiosReducer = <T>() =>
  function (state: AxiosState<T>, action: Actions<T>): AxiosState<T> {
    switch (action.type) {
      case 'loading':
        return { ...state, error: undefined, loading: true };
      case 'error':
        return { ...state, error: action.payload, loading: false };
      case 'data':
        return {
          ...state,
          error: undefined,
          loading: false,
          data: action.payload,
        };
      case 'noRun':
        return { ...state, error: undefined, loading: false };
      default:
        return state;
    }
  };
