import React, { useMemo, useState, useEffect, useRef } from 'react';
import { groupBy, isEmpty } from 'lodash-es';
import {
  Menu,
  Icon,
  IconProps,
  Button,
  Dropdown,
  Popup,
} from 'semantic-ui-react';
import {
  QuickViewsConfig,
  QuickView,
  View,
  QuickViewConfig,
  BaseGrouping,
} from 'types';
import Axios, { Canceler } from 'axios';
import { useAutoControlled } from './hooks/useAutoControlled';
import QuickViewModal, { QuickViewModalProps } from './QuickViewModal';

const stubObj = {};
const sortOrderToRightType: { [key: string]: 'asc' | 'desc' } = {
  ascending: 'asc',
  descending: 'desc',
  asc: 'asc',
  desc: 'desc',
};

export interface QuickViewsProps {
  /**
   * A collection of views that are easily switched between. There are options to make them user-addable as well as remotely stored through a service and database. Setting this enables basic quick views. Quick Views work by changing the current setting of the grid to their view. They don't lock the grid to that view, however.
   */
  config?: QuickViewsConfig;
  /**
   * The Id with which to store the quickViews either locally or remotely. Setting this enables custom quick views.
   */
  id?: string;
  /**
   * The URL to use to GET quickViews/ PUT the updated quick views
   */
  url?: string;
  /**
   * The ID of the quick view to show. Makes the quick view controlled.
   * Prevents nonprogramtic changing of the quick view (this is useful for cases where you might want some other part of the app to control the current QV).
   */
  value?: string;
  /**
   * The ID of the default quick view to show upon first displaying the grid
   */
  defaultValue?: string;
  /**
   * The Id to use to save the quick views to the database using
   */
  ownerId?: string;
  /**
   * If set to true, disables the ability to create and edit quick views
   */
  disableChanges?: boolean;
  /**
   * If set to true, hides the menu entirely
   */
  disableMenu?: boolean;
  /**
   * A function to handle quick view changes. Will let you control which quick view is showing if you use the quickView prop.
   */
  onChange?: (quickView: string, config: QuickView) => void;

  onViewChange: (config: QuickView) => void;
  /**
   * An optional way to add a share button if you want to generate a link to the quick view.
   * It's only really useful with the quickViewURL and controlled quickviews, as you would be able to generate a link with the ownerId and quickView such that anyone can open it and see their quick views.
   * disableQuickViewEditing is also good for this.
   */
  onShare?: (quickView: string, config: QuickView) => void;
  /**
   * A function to get the current view settings
   */
  getView?: () => View;
}

export function QuickViews(props: QuickViewsProps) {
  const [open, setOpen] = useState(false);
  const [modalOpen, setModalOpen] = useState(false);
  const [editView, setEditView] = useState<QuickView | undefined>(undefined);

  const getViewRef = useRef(props.getView);
  getViewRef.current = props.getView;

  const [customConfig, setCustomConfig] = useState<QuickViewsConfig>(stubObj);
  const customConfigRef = useRef(stubObj);
  const disableChanges = props.disableChanges || !props.id;

  useEffect(() => {
    if (disableChanges || customConfig === customConfigRef.current) {
      return;
    }
    // Save the quick views!
    (async () => {
      if (props.url) {
        const params = {
          quickViewsId: props.id,
          ownerId: props.ownerId,
          quickViews: JSON.stringify(customConfig),
        };
        try {
          await Axios.put(props.url, params);
        } catch (error) {
          alert('Failed to save quickviews to the service');
          console.error(error);
        }
      } else {
        const storageKey = `${process.env.REACT_APP_NAME}.${props.id}.quickViews`;
        localStorage.setItem(storageKey, JSON.stringify(customConfig));
      }
    })();
  }, [customConfig, props.ownerId, props.id, props.url, disableChanges]);

  const config = useMemo(() => {
    if (!props.config) {
      return customConfig;
    } else if (isEmpty(customConfig)) {
      return props.config || customConfig;
    } else {
      return { ...props.config, ...customConfig };
    }
  }, [customConfig, props.config]);
  const quickViews = useMemo<QuickView[]>(() => {
    return Object.entries(config).map(([name, qv]) => ({ name, ...qv }));
  }, [config]);
  const [value, setValue] = useAutoControlled<string>({
    defaultValue: props.defaultValue,
    value: props.value,
    initialValue: '',
  });
  const view = useMemo(() => quickViews.find((qv) => qv.name === value), [
    value,
    quickViews,
  ]);
  useEffect(() => {
    const onViewChange = props.onViewChange;
    if (view) {
      onViewChange(view);
    }
  }, [view, props.onViewChange]);
  useEffect(() => {
    let cancelToken: Canceler | undefined;
    (async () => {
      let remoteQuickViews: QuickViewsConfig = {};
      if (props.id) {
        if (props.url && props.ownerId) {
          const params = {
            quickViewsId: props.id,
            ownerId: props.ownerId,
            appName: process.env.REACT_APP_NAME,
          };
          try {
            remoteQuickViews = (
              await Axios.get<QuickViewsConfig>(props.url, {
                params,
                cancelToken: new Axios.CancelToken((c) => (cancelToken = c)),
              })
            ).data;
          } catch (error) {
            if (!Axios.isCancel(error)) {
              console.error(error);
              alert('Failed to get quickviews from the service');
            }
          }
        } else {
          const storageKey = `${process.env.REACT_APP_NAME}.${props.id}.quickViews`;
          remoteQuickViews = JSON.parse(
            localStorage.getItem(storageKey) || '{}'
          );
        }
      }
      Object.values(remoteQuickViews).forEach((qv) => {
        qv.view.grouping = qv.view.grouping.map<BaseGrouping>(
          (group: BaseGrouping & { col_id?: string }) => ({
            colId: group.colId ?? group.col_id,
            sortOrder: group.sortOrder,
          })
        );
        qv.view.columns = qv.view.columns?.map(
          (col: { id: string; ID?: string; hidden: boolean }) => ({
            id: col.id ?? col.ID,
            hidden: col.hidden,
          })
        );
        qv.view.sortOrder =
          qv.view.sortOrder && sortOrderToRightType[qv.view.sortOrder];
      });
      customConfigRef.current = remoteQuickViews;
      setCustomConfig(remoteQuickViews);
    })();
    return cancelToken;
  }, [props.ownerId, props.id, props.url]);
  const handleChange = (_ev, data) => {
    setOpen(false);
    const qv: QuickView = data['data-qv'];
    if (qv.name === value) {
      props.onViewChange(qv);
    } else {
      props.onChange?.(qv.name, qv);
      setValue(qv.name);
    }
  };

  const handleCreate = () => {
    setOpen(false);
    setEditView(undefined);
    setModalOpen(true);
  };

  const handleRemove = (ev, data) => {
    ev.stopPropagation();
    setOpen(false);
    const { name } = data;
    setCustomConfig((prev) => {
      const { [name]: _old, ...config } = prev;
      return config;
    });
  };

  const handleEdit = (ev, data) => {
    ev.stopPropagation();
    setOpen(false);
    setEditView(data['data-qv']);
    setModalOpen(true);
  };

  const handleShare = (ev, data) => {
    ev.stopPropagation();
    setOpen(false);
    props.onShare?.(data.name, data['data-qv']);
  };

  const handleCreateQuickView: QuickViewModalProps['onCreateQuickView'] = (
    name,
    group,
    icon,
    color
  ) => {
    const view = getViewRef.current?.();
    if (!view) {
      return;
    }
    const props: QuickViewConfig = {
      custom: true,
      group,
      icon,
      color,
      view,
    };
    setCustomConfig((prev) => ({ ...prev, [name]: props }));
  };

  const handleEditQuickView: QuickViewModalProps['onEditQuickView'] = (
    name,
    newName,
    group,
    icon,
    color,
    updateData
  ) => {
    let view = editView?.view;
    if (updateData) {
      view = getViewRef.current?.();
    }
    if (!view) {
      return;
    }
    const props: QuickViewConfig = {
      custom: true,
      group,
      icon,
      color,
      view,
    };
    setCustomConfig((prev) => {
      const { [name]: old, ...values } = prev;
      return { ...values, [newName]: props };
    });
  };

  const quickViewGroups = useMemo(() => groupBy(quickViews, 'group'), [
    quickViews,
  ]);

  const quickViewMenu = Object.entries(quickViewGroups).map(
    ([groupName, qvGroup]) => {
      const items = qvGroup.map((qv) => (
        <Menu.Item
          key={qv.name}
          view={qv.view}
          name={qv.name}
          data-custom={qv.custom}
          data-qv={qv}
          onClick={handleChange}
          style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
          }}
        >
          {qv.icon && (
            <Icon
              className={`quickViewIcon ${qv.color}`}
              name={qv.icon as IconProps['name']}
            />
          )}
          {qv.name}
          <div>
            {qv.custom && props.onShare && (
              <Button
                title="Share View"
                icon={<Icon name="share square" color="black" />}
                compact
                size="mini"
                name={qv.name}
                data-qv={qv}
                onClick={handleShare}
                className="QHGrid--ButtonClear"
              />
            )}
            {qv.custom && !props.disableChanges && (
              <Button
                title="Edit View"
                icon={<Icon name="pencil" color="black" />}
                compact
                size="mini"
                name={qv.name}
                data-qv={qv}
                onClick={handleEdit}
                className="QHGrid--ButtonClear"
              />
            )}
            {qv.custom && !props.disableChanges && (
              <Button
                title="Delete View"
                icon={<Icon name="remove" color="red" />}
                color="red"
                compact
                size="mini"
                name={qv.name}
                onClick={handleRemove}
                className="QHGrid--ButtonClear"
              />
            )}
          </div>
        </Menu.Item>
      ));
      if (groupName !== 'undefined') {
        return (
          <Dropdown key={groupName} item text={groupName}>
            <Dropdown.Menu>{items}</Dropdown.Menu>
          </Dropdown>
        );
      } else {
        return items;
      }
    }
  );
  if (props.disableMenu || (!quickViews.length && disableChanges)) {
    return null;
  }
  return (
    <>
      <QuickViewModal
        open={modalOpen}
        onClose={() => setModalOpen((prev) => !prev)}
        quickViews={quickViews}
        onCreateQuickView={handleCreateQuickView}
        onEditQuickView={handleEditQuickView}
        values={editView}
      />
      <Popup
        trigger={
          <Button inverted>
            <Icon name="lightning" />
            Quick Views
          </Button>
        }
        position="bottom center"
        on="click"
        open={open}
        onOpen={() => setOpen(true)}
        onClose={() => setOpen(false)}
        name="quick"
        flowing
        basic
      >
        <Menu vertical fluid>
          {!disableChanges && (
            <Menu.Item onClick={handleCreate}>
              <Icon name="add" color="green" />
              Add Quick View
            </Menu.Item>
          )}
          {quickViewMenu}
        </Menu>
      </Popup>
    </>
  );
}
