import update from 'immutability-helper';
import { some } from 'lodash-es';
import React, {
  ReactNode,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import { List } from 'react-virtualized';
import {
  Button,
  Divider,
  Dropdown,
  Icon,
  Input,
  InputOnChangeData,
  Loader,
  Popup,
  Tab,
} from 'semantic-ui-react';

import { MFBaseProps, RMFBaseProps } from 'types';

import { useAxiosGet } from './hooks/axios';

export function getAccessorValue(
  opt: string,
  accessor: MFBaseProps['accessor']
) {
  if (accessor == null) {
    return opt;
  }
  switch (typeof accessor) {
    case 'function':
      return accessor(opt);
    case 'object':
      return accessor[opt];
    default:
      return opt[accessor];
  }
}
const ListSwitchLength = 300;
const listHeight = 215;
const baseTrigger = <Icon name="filter" />;
const stopPropFunc = (evt) => evt.stopPropagation();
const noVertPadding = { paddingBottom: 0, paddingTop: 0 };
const stubArray = [];

// type filterAccessor = string | number | object | ((item: T) => any);

export interface MFProps<T> extends MFBaseProps {
  selectedOpts?: string[];
  data: T[];
  field: string;
  onChange: (opts?: string[]) => void;
  accessor: any;
  trigger: ReactNode;
}
export function MultiFilterDropdown<T>({
  selectedOpts = stubArray,
  data,
  field,
  onChange,
  accessor,
  trigger = baseTrigger,
  ...props
}: MFProps<T>) {
  const [search, setSearch] = useState('');
  const [showSelected, setShowSelected] = useState(0);
  const options: string[] = useMemo(() => {
    return Array.from(
      new Set(data.flatMap((item) => item[field]).map((val) => `${val}`))
    ).sort();
  }, [data, field]);
  const listRef = useRef<List>(null);
  const filteredOptions: string[] = useMemo(() => {
    if (showSelected) {
      return [];
    }
    const opts = options;
    const lowerSearch = search.trim().toLowerCase();
    if (!lowerSearch) {
      return opts;
    }
    return opts.filter((option) =>
      option.toLowerCase().trim().includes(lowerSearch)
    );
  }, [options, search, showSelected]);
  const filteredSelectedOpts: string[] = useMemo(() => {
    if (!showSelected) {
      return [];
    }
    const opts = selectedOpts;
    const lowerSearch = search.trim().toLowerCase();
    if (!lowerSearch) {
      return opts;
    }
    return opts.filter((option) =>
      option.toLowerCase().trim().includes(lowerSearch)
    );
  }, [search, selectedOpts, showSelected]);
  const filteredOpts = showSelected ? filteredSelectedOpts : filteredOptions;
  const selectedOptsMap = useMemo(() => {
    return Object.fromEntries(selectedOpts.map((opt) => [opt, true]));
  }, [selectedOpts]);
  useEffect(() => {
    if (filteredOpts.length > ListSwitchLength) {
      listRef.current?.forceUpdateGrid();
    }
  }, [filteredOpts.length]);
  const handleSelectionChange = (evt, data) => {
    evt.stopPropagation();
    const isSelected = selectedOpts.includes(data.value);
    let newSelectedOpts: string[] = selectedOpts;
    if (isSelected) {
      newSelectedOpts = newSelectedOpts.filter((opt) => opt !== data.value);
    } else {
      newSelectedOpts = newSelectedOpts.concat(data.value);
    }
    onChange(newSelectedOpts.length ? newSelectedOpts : undefined);
  };
  const handleClearAll = (evt) => {
    evt.stopPropagation();
    const newSelectedOpts = selectedOpts.filter(
      (opt) => !filteredOpts.includes(opt)
    );
    onChange(newSelectedOpts.length ? newSelectedOpts : undefined);
    if (props.clearSearchOnSelectOrClearAll) {
      setSearch('');
    }
  };

  const handleSelectAll = (evt) => {
    evt.stopPropagation();
    onChange(Array.from(new Set([...filteredOpts, ...selectedOpts])));
    if (props.clearSearchOnSelectOrClearAll) {
      setSearch('');
    }
  };
  const rowRenderer = ({ key, index, style }) => {
    const opt = filteredOpts[index];
    return (
      <Dropdown.Item
        className="dropdownItem"
        key={key}
        value={opt}
        onClick={handleSelectionChange}
        style={style}
      >
        {selectedOptsMap[opt] && <Icon color="green" name="checkmark" />}
        {getAccessorValue(opt, accessor)}
      </Dropdown.Item>
    );
  };
  return (
    <Popup
      position="bottom center"
      trigger={<span onClick={stopPropFunc}>{trigger}</span>}
      className="MultiFilterPopup"
      on="click"
      onClick={stopPropFunc}
      popperDependencies={[filteredOpts.length]}
    >
      <Popup.Content className="Filter MultiFilter">
        <Tab
          panes={[
            { menuItem: 'All', pane: <div /> },
            {
              menuItem: `Selected ${
                selectedOpts.length ? `(${selectedOpts.length})` : ''
              }`,
              pane: <div />,
            },
          ]}
          activeIndex={showSelected}
          onTabChange={(_evt, data) =>
            setShowSelected(data.activeIndex as number)
          }
        />
        <div className=" dropdown ui">
          <div
            className="menu transition visible"
            style={{ position: 'unset' }}
          >
            <Input
              icon="search"
              iconPosition="left"
              className="search"
              placeholder="search"
              value={search}
              onChange={(_evt, data) => setSearch(data.value)}
              onClick={stopPropFunc}
              action={{
                color: 'red',
                icon: { name: 'x', style: noVertPadding },
                onClick: () => setSearch(''),
                title: 'Clear Search',
                style: noVertPadding,
              }}
            />
            {filteredOpts.length > ListSwitchLength && (
              <List
                ref={listRef}
                height={listHeight}
                rowCount={filteredOpts.length}
                rowHeight={36}
                rowRenderer={rowRenderer}
                width={400}
                autoContainerWidth
                className="dropdownMenu"
              />
            )}
            {filteredOpts.length <= ListSwitchLength && (
              <Dropdown.Menu
                scrolling
                style={{ maxWidth: '25vw', maxHeight: `${listHeight}px` }}
              >
                {filteredOpts.map((opt, idx) => {
                  return (
                    <Dropdown.Item
                      key={idx}
                      value={opt}
                      onClick={handleSelectionChange}
                    >
                      {selectedOptsMap[opt] && (
                        <Icon color="green" name="checkmark" />
                      )}
                      {getAccessorValue(opt, accessor)}
                    </Dropdown.Item>
                  );
                })}
              </Dropdown.Menu>
            )}
            {/* <Divider horizontal /> */}
            <Button.Group fluid widths={2}>
              <Button
                disabled={filteredOpts.length > 500 || !!showSelected}
                title={
                  filteredOptions.length > 500
                    ? 'Disabled due to large number of options'
                    : undefined
                }
                positive
                onClick={handleSelectAll}
              >
                Select All
              </Button>
              <Button negative onClick={handleClearAll}>
                Clear
              </Button>
            </Button.Group>
          </div>
        </div>
      </Popup.Content>
    </Popup>
  );
}

export interface RMFProps<T> extends RMFBaseProps {
  selectedOpts?: string[];
  data: T[];
  field: string;
  onChange: (opts?: string[]) => void;
  trigger: ReactNode;
}
export function RemoteMultiFilterDropdown<T>({
  selectedOpts = stubArray,
  data,
  field,
  onChange,
  accessor,
  trigger = baseTrigger,
  useLocal,
  ...props
}: RMFProps<T>) {
  const [search, setSearch] = useState('');
  const [showSelected, setShowSelected] = useState(0);
  const [opened, setOpened] = useState(false);
  const {
    data: remoteOptions = stubArray,
    loading,
    trigger: loadOptions,
  } = useAxiosGet<string[]>(props.remoteUrl, props.remoteParams, {
    runIf: () => props.prefetch || opened,
    processData: (data) => data.map((val) => `${val}`).sort(),
  });
  useEffect(() => {
    if (!props.prefetch) {
      loadOptions();
    }
  }, [opened, loadOptions, props.prefetch]);
  const localOptions: string[] = useMemo(() => {
    if (!useLocal) {
      return stubArray;
    }
    return Array.from(
      new Set(data.flatMap((item) => item[field]).map((val) => `${val}`))
    ).sort();
  }, [data, useLocal, field]);
  const options = useMemo(() => {
    if (!localOptions.length) {
      return remoteOptions;
    }
    return Array.from(new Set([...remoteOptions, ...localOptions])).sort();
  }, [remoteOptions, localOptions]);
  const listRef = useRef<List>(null);
  const filteredOptions: string[] = useMemo(() => {
    if (showSelected) {
      return [];
    }
    const opts = options;
    const lowerSearch = search.trim().toLowerCase();
    if (!lowerSearch) {
      return opts;
    }
    return opts.filter((option) =>
      option.toLowerCase().trim().includes(lowerSearch)
    );
  }, [options, search, showSelected]);
  const filteredSelectedOpts: string[] = useMemo(() => {
    if (!showSelected) {
      return [];
    }
    const opts = selectedOpts;
    const lowerSearch = search.trim().toLowerCase();
    if (!lowerSearch) {
      return opts;
    }
    return opts.filter((option) =>
      option.toLowerCase().trim().includes(lowerSearch)
    );
  }, [search, selectedOpts, showSelected]);
  const filteredOpts = showSelected ? filteredSelectedOpts : filteredOptions;
  const selectedOptsMap = useMemo(() => {
    return Object.fromEntries(selectedOpts.map((opt) => [opt, true]));
  }, [selectedOpts]);
  useEffect(() => {
    if (filteredOpts.length > ListSwitchLength) {
      listRef.current?.forceUpdateGrid();
    }
  }, [filteredOpts.length]);
  const handleSelectionChange = (evt, data) => {
    evt.stopPropagation();
    const isSelected = selectedOpts.includes(data.value);
    let newSelectedOpts: string[] = selectedOpts;
    if (isSelected) {
      newSelectedOpts = newSelectedOpts.filter((opt) => opt !== data.value);
    } else {
      newSelectedOpts = newSelectedOpts.concat(data.value);
    }
    onChange(newSelectedOpts.length ? newSelectedOpts : undefined);
  };
  const handleClearAll = (evt) => {
    evt.stopPropagation();
    const newSelectedOpts = selectedOpts.filter(
      (opt) => !filteredOpts.includes(opt)
    );
    onChange(newSelectedOpts.length ? newSelectedOpts : undefined);
    if (props.clearSearchOnSelectOrClearAll) {
      setSearch('');
    }
  };

  const handleSelectAll = (evt) => {
    evt.stopPropagation();
    onChange(Array.from(new Set([...filteredOpts, ...selectedOpts])));
    if (props.clearSearchOnSelectOrClearAll) {
      setSearch('');
    }
  };
  const rowRenderer = ({ key, index, style }) => {
    const opt = filteredOpts[index];
    return (
      <Dropdown.Item
        className="dropdownItem"
        key={key}
        value={opt}
        onClick={handleSelectionChange}
        style={style}
      >
        {selectedOptsMap[opt] && <Icon color="green" name="checkmark" />}
        {getAccessorValue(opt, accessor)}
      </Dropdown.Item>
    );
  };
  return (
    <Popup
      position="bottom center"
      trigger={<span onClick={stopPropFunc}>{trigger}</span>}
      className="MultiFilterPopup"
      on="click"
      onOpen={() => setOpened(true)}
      onClick={stopPropFunc}
      popperDependencies={[filteredOpts.length]}
    >
      <Popup.Content className="Filter MultiFilter">
        <Loader active={loading} />
        <Tab
          panes={[
            { menuItem: 'All', pane: <div /> },
            {
              menuItem: `Selected ${
                selectedOpts.length ? `(${selectedOpts.length})` : ''
              }`,
              pane: <div />,
            },
          ]}
          activeIndex={showSelected}
          onTabChange={(_evt, data) =>
            setShowSelected(data.activeIndex as number)
          }
        />
        <div className=" dropdown ui">
          <div
            className="menu transition visible"
            style={{ position: 'unset' }}
          >
            <Input
              icon="search"
              iconPosition="left"
              className="search"
              placeholder="search"
              value={search}
              onChange={(_evt, data) => setSearch(data.value)}
              onClick={stopPropFunc}
              action={{
                color: 'red',
                icon: { name: 'x', style: noVertPadding },
                onClick: () => setSearch(''),
                title: 'Clear Search',
                style: noVertPadding,
              }}
            />
            {filteredOpts.length > ListSwitchLength && (
              <List
                ref={listRef}
                height={listHeight}
                rowCount={filteredOpts.length}
                rowHeight={36}
                rowRenderer={rowRenderer}
                width={400}
                autoContainerWidth
                className="dropdownMenu"
              />
            )}
            {filteredOpts.length <= ListSwitchLength && (
              <Dropdown.Menu
                scrolling
                style={{ maxWidth: '25vw', maxHeight: `${listHeight}px` }}
              >
                {filteredOpts.map((opt, idx) => {
                  return (
                    <Dropdown.Item
                      key={idx}
                      value={opt}
                      onClick={handleSelectionChange}
                    >
                      {selectedOptsMap[opt] && (
                        <Icon color="green" name="checkmark" />
                      )}
                      {getAccessorValue(opt, accessor)}
                    </Dropdown.Item>
                  );
                })}
              </Dropdown.Menu>
            )}
            {/* <Divider horizontal /> */}
            <Button.Group fluid widths={2}>
              <Button
                disabled={filteredOpts.length > 500 || !!showSelected}
                title={
                  filteredOptions.length > 500
                    ? 'Disabled due to large number of options'
                    : undefined
                }
                positive
                onClick={handleSelectAll}
              >
                Select All
              </Button>
              <Button negative onClick={handleClearAll}>
                Clear
              </Button>
            </Button.Group>
          </div>
        </div>
      </Popup.Content>
    </Popup>
  );
}

export interface DateFilterOpts {
  startDate: string;
  endDate: string;
}
export interface DateFilterProps {
  onChange: (opts?: DateFilterOpts) => void;
  selectedOpts?: DateFilterOpts;
  trigger?: Node;
}
const initialDateFilterOpts = { startDate: '', endDate: '' };

export const DateFilterDropdown: React.FunctionComponent<DateFilterProps> = function DateFilterDropdown(
  props
) {
  const { selectedOpts = initialDateFilterOpts, trigger = baseTrigger } = props;
  const handleChange = (evt, { value, name }: InputOnChangeData) => {
    evt.stopPropagation();
    const newOpts = { ...selectedOpts, [name]: value };
    if (!some(newOpts, Boolean)) {
      props.onChange?.();
    } else {
      props.onChange?.(newOpts);
    }
  };
  const { startDate, endDate } = selectedOpts;
  return (
    <Popup
      position="bottom center"
      trigger={<span onClick={stopPropFunc}>{trigger}</span>}
      className="DateFilterDropdown"
      on="click"
      onClick={stopPropFunc}
    >
      <Popup.Content className="Filter DateFilter">
        <Input
          onClick={stopPropFunc}
          icon={{
            name: 'x',
            style: { pointerEvents: 'all' },
            onClick: (e) => {
              handleChange(e, { value: '', name: 'startDate' });
            },
            'aria-label': 'Clear',
          }}
          label={{ content: 'From:', color: 'blue', htmlFor: 'startDate' }}
          labelPosition="left"
          type="date"
          fluid
          style={{ cursor: 'pointer' }}
          value={startDate}
          name="startDate"
          onChange={handleChange}
        />
        <Input
          onClick={stopPropFunc}
          icon={{
            name: 'x',
            style: { pointerEvents: 'all' },
            onClick: (e) => {
              handleChange(e, { value: '', name: 'endDate' });
            },
            'aria-label': 'Clear',
          }}
          label={{ content: 'Until:', color: 'blue', htmlFor: 'endDate' }}
          labelPosition="left"
          type="date"
          fluid
          style={{ cursor: 'pointer' }}
          value={endDate}
          name="endDate"
          onChange={handleChange}
        />
      </Popup.Content>
    </Popup>
  );
};

const numericComparisons = [
  {
    text: 'Is equal to',
    value: '=',
  },
  {
    text: 'Is not equal to',
    value: '!=',
  },
  {
    text: 'Is greater than or equal to',
    value: '>=',
  },
  {
    text: 'Is greater than',
    value: '>',
  },
  {
    text: 'Is less than or equal to',
    value: '<=',
  },
  {
    text: 'Is less than',
    value: '<',
  },
  {
    text: 'Is null',
    value: 'null',
  },
  {
    text: 'Is not null',
    value: '!null',
  },
];
enum Comparisons {
  equals = '=',
  notEquals = '!=',
  greaterOrEqual = '>=',
  greater = '>',
  lessOrEqual = '<=',
  less = '<',
  null = 'null',
  notNull = '!null',
  contains = 'contains',
  notContains = '!contains',
  starts = 'starts',
  ends = 'ends',
  empty = 'empty',
  notEmpty = '!empty',
}
const singleComparisons = {
  null: true,
  '!null': true,
  empty: true,
  '!empty': true,
};
enum Combinations {
  and = '&&',
  or = '||',
}
export interface AlphanumericOpt {
  comparison: Comparisons;
  combination: Combinations;
  value: string;
}
export interface AlphanumericFilterProps {
  onChange: (opts?: AlphanumericOpt[]) => void;
  selectedOpts: AlphanumericOpt[];
  trigger: Node;
}
const combinations: { text: string; value: Combinations }[] = [
  {
    text: 'And',
    value: Combinations.and,
  },
  { text: 'Or', value: Combinations.or },
];
const numericInitialState: AlphanumericOpt = {
  combination: Combinations.and,
  comparison: Comparisons.equals,
  value: '',
};

const isComparisonActive = (opts) =>
  singleComparisons[opts.comparison] || opts.value;
function useComparisonFilter(
  selectedOptsProp: AlphanumericOpt[],
  onChange: (opts?: AlphanumericOpt[]) => void,
  initialState: AlphanumericOpt
) {
  const [selectedOpts, setSelectedOpts] = useState(selectedOptsProp);
  const selectedOptsRef = useRef(selectedOpts);
  useEffect(() => {
    selectedOptsRef.current = selectedOpts;
  }, [selectedOpts]);
  const handleAdd = useCallback(
    (_evt) => {
      setSelectedOpts((selectedOpts) =>
        update(selectedOpts, { $push: [initialState] })
      );
    },
    [initialState]
  );
  const handleChange = useCallback((evt, { value, name, 'data-idx': idx }) => {
    evt.stopPropagation();
    setSelectedOpts((selectedOpts) =>
      update(selectedOpts, { [idx]: { [name]: { $set: value } } })
    );
  }, []);
  const handleClear = useCallback(() => {
    setSelectedOpts([initialState]);
    onChange();
  }, [onChange, initialState]);
  const handleFilter = useCallback(() => {
    if (!selectedOptsRef.current.some(isComparisonActive)) {
      onChange();
    } else {
      onChange(selectedOptsRef.current);
    }
  }, [onChange]);
  const handleOpen = useCallback(() => {
    if (selectedOptsProp.some(isComparisonActive)) {
      setSelectedOpts(selectedOptsProp);
    }
  }, [selectedOptsProp]);
  const handleRemove = useCallback((_evt, { 'data-idx': idx }) => {
    setSelectedOpts((selectedOpts) =>
      update(selectedOpts, { $splice: [[idx, 1]] })
    );
  }, []);
  return {
    handleAdd,
    handleChange,
    handleClear,
    handleFilter,
    handleOpen,
    handleRemove,
    selectedOpts,
  };
}

export const NumericFilterPopup = React.memo<AlphanumericFilterProps>(
  function NumericFilterPopup({ onChange, ...props }) {
    const filter = useComparisonFilter(
      props.selectedOpts ?? [numericInitialState],
      onChange,
      numericInitialState
    );
    const filterInputs = filter.selectedOpts.map((opts, idx) => {
      const combination =
        idx > 0 ? (
          <Dropdown
            data-idx={idx}
            name="combination"
            options={combinations}
            selection
            value={opts.combination}
            onChange={filter.handleChange}
            className="combination"
            compact
          />
        ) : null;
      return (
        <div className="filter" key={idx}>
          {idx > 0 &&
            (opts.combination === '&&' ? (
              combination
            ) : (
              <Divider horizontal>{combination}</Divider>
            ))}
          <div className="values">
            <div>
              <Dropdown
                data-idx={idx}
                name="comparison"
                options={numericComparisons}
                selection
                value={opts.comparison}
                onChange={filter.handleChange}
              />
              {!singleComparisons[opts.comparison] && (
                <Input
                  data-idx={idx}
                  name="value"
                  type="number"
                  step="any"
                  value={opts.value}
                  onChange={filter.handleChange}
                />
              )}
            </div>
            <Button
              icon={{ name: 'minus', color: 'red' }}
              compact
              disabled={idx === 0}
              onClick={filter.handleRemove}
              data-idx={idx}
            />
          </div>
        </div>
      );
    });
    return (
      <Popup
        position="bottom center"
        trigger={<span onClick={stopPropFunc}>{props.trigger}</span>}
        className="ComparisonFilterPopup"
        on="click"
        onOpen={filter.handleOpen}
        onClick={stopPropFunc}
      >
        <Popup.Content className="ComparisonFilter">
          {filterInputs}
          <Divider horizontal>
            <Button
              className="add"
              icon={{ name: 'plus circle', color: 'green', size: 'large' }}
              compact
              onClick={filter.handleAdd}
            />
          </Divider>
          <Button.Group
            fluid
            widths={2}
            style={{ position: 'sticky', bottom: '0px' }}
          >
            <Button positive onClick={filter.handleFilter}>
              Filter
            </Button>
            <Button negative onClick={filter.handleClear}>
              Clear
            </Button>
          </Button.Group>
        </Popup.Content>
      </Popup>
    );
  }
);

const alphanumericComparisons: { text: string; value: Comparisons }[] = [
  {
    text: 'Is equal to',
    value: Comparisons.equals,
  },
  {
    text: 'Is not equal to',
    value: Comparisons.notEquals,
  },
  {
    text: 'Contains',
    value: Comparisons.contains,
  },
  {
    text: 'Does not Contain',
    value: Comparisons.notContains,
  },
  {
    text: 'Starts with',
    value: Comparisons.starts,
  },
  {
    text: 'Ends with',
    value: Comparisons.ends,
  },
  {
    text: 'Is null',
    value: Comparisons.null,
  },
  {
    text: 'Is not null',
    value: Comparisons.notNull,
  },
  {
    text: 'Is empty',
    value: Comparisons.empty,
  },
  {
    text: 'Is not empty',
    value: Comparisons.notEmpty,
  },
];
const alphanumericInitialState: {
  comparison: Comparisons;
  combination: Combinations;
  value: string;
} = {
  combination: Combinations.and,
  comparison: Comparisons.contains,
  value: '',
};

export const AlphanumericFilterPopup: React.FC<AlphanumericFilterProps> = function AlphanumericFilterPopup({
  onChange,
  ...props
}) {
  const filter = useComparisonFilter(
    props.selectedOpts ?? [alphanumericInitialState],
    onChange,
    alphanumericInitialState
  );
  const filterInputs = filter.selectedOpts.map((opts, idx) => {
    const combination =
      idx > 0 ? (
        <Dropdown
          data-idx={idx}
          name="combination"
          options={combinations}
          selection
          value={opts.combination}
          onChange={filter.handleChange}
          className="combination"
          compact
        />
      ) : null;
    return (
      <div className="filter" key={idx}>
        {idx > 0 &&
          (opts.combination === '&&' ? (
            combination
          ) : (
            <Divider horizontal>{combination}</Divider>
          ))}
        <div className="values">
          <div>
            <Dropdown
              data-idx={idx}
              name="comparison"
              options={alphanumericComparisons}
              selection
              value={opts.comparison}
              onChange={filter.handleChange}
            />
            {!singleComparisons[opts.comparison] && (
              <Input
                data-idx={idx}
                name="value"
                // type="number"
                // step="any"
                value={opts.value}
                onChange={filter.handleChange}
              />
            )}
          </div>
          <Button
            icon={{ name: 'minus', color: 'red' }}
            compact
            disabled={idx === 0}
            onClick={filter.handleRemove}
            data-idx={idx}
          />
        </div>
      </div>
    );
  });
  return (
    <Popup
      position="bottom center"
      trigger={<span onClick={stopPropFunc}>{props.trigger}</span>}
      className="ComparisonFilterPopup"
      on="click"
      onOpen={filter.handleOpen}
      onClick={stopPropFunc}
    >
      <Popup.Content className="ComparisonFilter">
        {filterInputs}
        <Divider horizontal>
          <Button
            className="add"
            icon={{ name: 'plus circle', color: 'green', size: 'large' }}
            compact
            onClick={filter.handleAdd}
          />
        </Divider>
        <Button.Group
          fluid
          widths={2}
          style={{ position: 'sticky', bottom: '0px' }}
        >
          <Button positive onClick={filter.handleFilter}>
            Filter
          </Button>
          <Button negative onClick={filter.handleClear}>
            Clear
          </Button>
        </Button.Group>
      </Popup.Content>
    </Popup>
  );
};
