import moment from 'moment';

import { ColumnConfig } from 'types';

export function getTemplate(column) {
  if (column.template) {
    return column.template;
  }
  if (typeMap[column.type]) {
    return typeMap[column.type].template;
  }
  return undefined;
}
export function getExportTemplate<T, ATI>(
  column: ColumnConfig<T, ATI>
): ColumnConfig<T, ATI>['exportTemplate'] | undefined {
  if (column.exportTemplate) {
    return column.exportTemplate;
  }
  return typeMap[column.type ?? '']?.exportTemplate;
  // if (column.type && typeMap[column.type]) {
  //   return typeMap[column.type].exportTemplate;
  // }
  // return undefined;
}
export function getFieldValue<T, ATI>(
  item: T,
  field: ColumnConfig<T, ATI>['field'],
  type?: ColumnConfig<T, ATI>['type']
): T[keyof T] | any {
  let fieldName: keyof T;
  let accessor: number | object | keyof T | typeof typeMap['']['accessor'] =
    typeMap[type || '']?.accessor;
  if (typeof field === 'object') {
    fieldName = field.field;
    if (field.accessor) accessor = field.accessor;
  } else {
    fieldName = field;
  }
  switch (typeof accessor) {
    case 'function':
      return accessor(item, fieldName);
    case 'object':
      return accessor[(item[fieldName] as unknown) as string];
    default:
      return item[(accessor as keyof T) || fieldName];
  }
}

export function getField<T, ATI>(field: ColumnConfig<T, ATI>['field']) {
  if (field === null) return field;
  switch (typeof field) {
    case 'object':
      return field.field;
    default:
      return field;
  }
}
export const typeMap: Record<
  string,
  {
    accessor?: (item: any, field: keyof typeof item) => any;
    sortAccessor?: (item: any, field: keyof typeof item) => any;
    groupAccessor?: (item: any, field: keyof typeof item) => any;
    exportTemplate?: (value: any) => string | number;
    template?: (value: any) => React.ReactNode;
  }
> = {
  number: {
    sortAccessor: (item, field) => Number(item[field]),
    groupAccessor: (item, field) => `#${item[field]}`,
  },
  date: {
    accessor: (item, field) => {
      if (!item[field]) return undefined;
      const date = moment(new Date(item[field] as string));
      return date.isValid() ? date : undefined; // .format('D/MMM/YYYY') : 'N/A';
      // return value ? value.format('D/MMM/Y') : 'N/A';
    },
    sortAccessor: (item, field) =>
      item[field] && new Date(item[field] as string).toISOString().slice(0, 10), // Remove the timestamp
    groupAccessor: (item, field) =>
      item[field] && moment(new Date(item[field] as string)).format('D/MMM/Y'),
    exportTemplate: (value) => (value ? value.format('D/MMM/Y') : 'N/A'),

    template: (value: moment.Moment) =>
      value ? value.format('D/MMM/Y') : 'N/A',
  },
  datetime: {
    accessor: (item, field) => {
      if (!item[field]) return undefined;
      const date = moment(new Date(item[field] as string));
      return date.isValid() ? date : undefined; // .format('D/MMM/YYYY') : 'N/A';
      // return value ? value.format('D/MMM/Y HH:mm:ss') : 'N/A';
    },
    sortAccessor: (item, field) =>
      item[field] && new Date(item[field] as string),
    groupAccessor: (item, field) =>
      item[field] && moment(new Date(item[field] as string)).format('D/MMM/Y'),
    exportTemplate: (value) =>
      value ? value.format('D/MMM/Y HH:mm:ss') : 'N/A',
    template: (value) => (value ? value.format('D/MMM/Y HH:mm:ss') : 'N/A'),
  },
};

export function getFieldGroupValueFunction<T>(
  field: ColumnConfig<T, never>['field'],
  type: ColumnConfig<T, never>['type']
): (item: T) => string {
  let fieldName: keyof T;
  let groupAccessor:
    | number
    | object
    | keyof T
    | typeof typeMap['']['groupAccessor'] = typeMap[type || '']?.groupAccessor;
  if (typeof field === 'object') {
    fieldName = field.field;
    if (field.groupAccessor) groupAccessor = field.groupAccessor;
  } else {
    fieldName = field;
  }
  switch (typeof groupAccessor) {
    case 'function': {
      const accessor = groupAccessor;
      return (item) => accessor(item, fieldName);
    }
    case 'object': {
      const accessor = groupAccessor;
      return (item) => accessor[(item[fieldName] as unknown) as string];
    }
    default: {
      const accessor = (groupAccessor || fieldName) as number | string;
      return (item) => item[accessor];
    }
  }
}
export function getFieldSortValueFunction<T>(
  field: ColumnConfig<T, never>['field'],
  type: ColumnConfig<T, never>['type']
): (item: T) => number | string {
  let fieldName: keyof T;
  let sortAccessor:
    | number
    | object
    | keyof T
    | typeof typeMap['']['sortAccessor'] = typeMap[type || '']?.sortAccessor;
  if (typeof field === 'object') {
    fieldName = field.field;
    if (field.sortAccessor) sortAccessor = field.sortAccessor;
  } else {
    fieldName = field;
  }
  switch (typeof sortAccessor) {
    case 'function': {
      const accessor = sortAccessor;
      return (item) => accessor(item, fieldName);
    }
    case 'object': {
      const accessor = sortAccessor;
      return (item) => accessor[(item[fieldName] as unknown) as string];
    }
    default: {
      const accessor = (sortAccessor || fieldName) as number | string;
      return (item) => {
        const val = item[accessor];
        return Array.isArray(val) ? val.toString() : val;
      };
    }
  }
}
